.label {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: pointer;
}
.checker {
  content: '';
  display: block;
  width: 22px;
  height: 22px;
  background: center center/cover no-repeat transparent;
  transition: background-image 0.3s;
  margin-right: 10px;
}

.design-brand {
  background-image: url(data:image/svg+xml;base64,);
}

.input {
  position: absolute;
  opacity: 0.01;
  width: 0.01px;
  height: 0.01px;

  &:checked + .checker {
    &.design-brand {
      background-image: url(data:image/svg+xml;base64,);
    }
  }

  &[disabled] {
    & ~ .content,
    & ~ .checker {
      cursor: default;
      color: var(--medium-grey);
    }
  }
}

.content {
  line-height: 1.4;
}
